.header {
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
    background-color: #08AEEA;
    background-image: -webkit-gradient(linear, left top, right top, from(#08AEEA), to(#2AF598));
    background-image: -webkit-linear-gradient(left, #08AEEA 0%, #2AF598 100%);
    background-image: -o-linear-gradient(left, #08AEEA 0%, #2AF598 100%);
    background-image: linear-gradient(90deg, #08AEEA 0%, #2AF598 100%);
    color: white;
    font-size: 2.5em;
    padding: 0.25em;
    margin-bottom: 0.5em;
}


